<template>
  <demo-section>
    <demo-block title="">
      <cell :showIcon="true" :isLink="true" @click.native="notify1('')">
        <span slot="title">
          <label></label>
        </span>
      </cell>
    </demo-block>
    <demo-block title="">
      <cell :showIcon="true" :isLink="true" @click.native="notify2('')">
        <span slot="title">
          <label></label>
        </span>
      </cell>
      <cell :showIcon="true" :isLink="true" @click.native="notify3('')">
        <span slot="title">
          <label></label>
        </span>
      </cell>
      <cell :showIcon="true" :isLink="true" @click.native="notify4('')">
        <span slot="title">
          <label></label>
        </span>
      </cell>
      <cell :showIcon="true" :isLink="true" @click.native="notify5('')">
        <span slot="title">
          <label></label>
        </span>
      </cell>
    </demo-block>
    <demo-block title="">
      <cell
        :showIcon="true"
        :isLink="true"
        @click.native="cusBgNotify('')"
      >
        <span slot="title">
          <label></label>
        </span>
      </cell>
    </demo-block>
    <demo-block title="">
      <cell
        :showIcon="true"
        :isLink="true"
        @click.native="timeNotify('')"
      >
        <span slot="title">
          <label></label>
        </span>
      </cell>
    </demo-block>
    <demo-block title="">
      <cell :showIcon="true" :isLink="true" @click.native="showNotify">
        <span slot="title">
          <label></label>
        </span>
      </cell>
      <notify :showPopup="show" type="success" msg="hello" duration="10000">
        <span>hello</span>
      </notify>
    </demo-block>
    <demo-block title="">
      <cell :showIcon="true" :isLink="true" @click.native="defaultNotify">
        <span slot="title">
          <label>Notify5000</label>
        </span>
      </cell>
      <cell :showIcon="true" :isLink="true" @click.native="resetDefaultOptions">
        <span slot="title">
          <label>Toast</label>
        </span>
      </cell>
    </demo-block>
  </demo-section>
</template>
<script>
import cell from '../../cell';
export default {
  components: {
    cell,
  },
  data() {
    return {
      show: false,
    };
  },
  methods: {
    notify1(val) {
      this.$notify.text(val, {
        onClosed() {
          console.log('close');
        },
        onClick: () => {
          console.log('click');
        },
        onOpened: () => {
          console.log('opened');
        },
      });
    },
    notify2(val) {
      this.$notify.primary(val);
    },
    notify3(val) {
      this.$notify.success(val);
    },
    notify4(val) {
      this.$notify.danger(val);
    },
    notify5(val) {
      this.$notify.warn(val);
    },
    cusBgNotify(val) {
      this.$notify.text(val, {
        color: '#ad0000',
        background: '#ffe1e1',
      });
    },
    timeNotify(val) {
      this.$notify.text(val, {
        duration: 10000,
      });
    },
    showNotify() {
      this.show = true;
      setTimeout(() => {
        this.show = false;
      }, 200);
    },
    defaultNotify() {
      this.$notify.setDefaultOptions({
        duration: 5000,
      });
      this.$notify.text('Notify5000');
    },
    resetDefaultOptions() {
      this.$notify.resetDefaultOptions();
      this.$notify.text('Notify');
    },
  },
};
</script>
