<template>
  <demo-section>
    <demo-block title="">
      <i-button @click="openDialog"> Dialog</i-button>
      <Dialog
        v-model="dialogVisible"
        title=""
        @confirm="handleConfirm"
      >
        <p></p>
      </Dialog>
    </demo-block>

    <demo-block title=" $dialog">
      <i-button @click="onClickAlert"> Dialog</i-button>
      <i-button @click="onClickAlert2"> Dialog</i-button>
    </demo-block>
  </demo-section>
</template>
<script>
//  Vue.use(Dialog);
import Dialog from '../index';

export default {
  // 
  components: {
    [Dialog.name]: Dialog.Component,
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  methods: {
    openDialog() {
      this.dialogVisible = true;
    },
    handleConfirm() {
      this.$toast('');
    },
    onClickAlert() {
      this.$dialog
        .alert({
          title: '',
          message: ' 1',
          confirmButtonColor: '#333',
        })
        .then(() => {
          this.handleConfirm();
        });
    },
    onClickAlert2() {
      this.$dialog
        .confirm({
          title: '',
          message: ' 2',
        })
        .then((action) => {
          this.$toast(action);
        })
        .catch((action) => {
          this.$toast(action);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
p {
  font-size: 16px;
}
</style>
