<template>
  <demo-section>
    <demo-block title="">
      <form-row title="">
        <form-input
          id="applicantName_input"
          v-model="applicantName"
          @blur="onBlur('applicantName', $event)"
          :warning="formWarning === 'applicantName'"
          @focus="onFocus('applicantName')"
          placeholder=""
        />
      </form-row>
      <form-row title="">
        <form-input
          id="applicantPhone_input"
          v-model="applicantPhone"
          @blur="onBlur('applicantPhone', $event)"
          :warning="formWarning === 'applicantPhone'"
          @focus="onFocus('applicantPhone')"
          placeholder=""
        />
      </form-row>
    </demo-block>
  </demo-section>
</template>
<script>
const name = function (name = '') {
  if (!/^[\u4e00-\u9fa5\sa-zA-Z_-]+$/.test(name)) {
    return ',';
  }
};
const mobile = function (number = '') {
  if (number.length == 0) {
    return '';
  }
  if (!/^1[3456789]\d{9}$/.test(number)) {
    if (number.length != 11) {
      return '11';
    }
    return '';
  }
  if (/(123456789|987654321)$/.test(number)) {
    return '';
  }
};
export default {
  data() {
    return {
      applicantName: '',
      applicantPhone: '',
      formWarning: '',
    };
  },
  methods: {
    onFocus(val) {},
    onBlur(type, val) {
      if (type === 'applicantName') {
        const warn = name(val);
        if (warn) {
          this.formWarning = 'applicantName';
          setTimeout(() => {
            this.$toast(warn);
          }, 0);
        }
      }
      if (type === 'applicantPhone') {
        const warn = mobile(val);
        if (warn) {
          this.formWarning = 'applicantPhone';
          setTimeout(() => {
            this.$toast(warn);
          }, 0);
        }
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.c-form-row {
  padding: 10px;
  background: #fff;
  border-radius: 0.1rem;
}
label {
  padding-left: 15px;
}
</style>
