<template>
  <demo-section>
    <demo-block title="">
      <steps v-model="activeIndex">
        <steps-item
          >iconlabel</steps-item
        >
        <steps-item
          ></steps-item
        >
        <steps-item
          > (autofocus  Safari
          )input
          </steps-item
        >
      </steps>
      <i-button type="primary" @click="prev"></i-button>
      <i-button class="gutter-top" type="primary" @click="next"
        ></i-button
      >
    </demo-block>
  </demo-section>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: 0,
    };
  },
  methods: {
    prev() {
      this.activeIndex = Math.max(0, this.activeIndex - 1);
    },

    next() {
      this.activeIndex = Math.min(2, this.activeIndex + 1);
    },
  },
};
</script>

